ravés de las competencias clave y los estándares de aprendizaje, además de definir uno de los pilares fundamentales de un buen proyecto, la evaluación, entendida siempre como un proceso constante de toma de datos para poder regular el aprendizaje. Finalmente, en la columna de la derecha, se define con precisión la estructura del proyecto, a través de los recursos necesarios, el uso de las TIC y los agrupamientos, y la gestión del ambiente contemplado en el diseño del proyecto de aprendizaje.")]),_vm._m(6)],1)],1)}
